.c {
  height: 100vh;
  position: relative;
}

.c-bg {
  width: 20px;
  height: 100%;
  background-color: #59b256;
  position: absolute;
}

.c-wrapper {
  padding: 50px;
  display: flex;
}

.c-left {
  flex: 1;
}

.c-right {
  flex: 1;
  display: flex;
  align-items: center;
  flex-direction: column;
  justify-content: center;
}

.c-info {
  position: relative;
  pointer-events: none;
}

.c-title {
  font-size: 60px;
  width: 70%;
  font-weight: 600;
}

.c-info-item {
  display: flex;
  align-items: center;
  margin-bottom: -4.5rem;
  font-weight: 300;
  pointer-events: none;
}

.c-icon {
  border: none;
  width: 10%;
  height: 100%;
}

.c-icon-mail {
  border: none;
  width: 10%;
  height: 100%;
  margin-bottom: 22px;
}

.c-desc {
  font-weight: 200;
}

form {
  width: 100%;
  margin-top: 20px;
  display: flex;
  flex-direction: column;
}

input {
  width: 50%;
  height: 50px;
  border: none;
  border-bottom: 1px solid black;
  margin: 10px 0px;
  font-size: 14px;
  padding-left: 10px;
}

textarea {
  width: 90%;
  margin: 10px 0px;
  font-size: 14px;
  padding-left: 10px;
  padding-top: 10px;
}

button {
  border: none;
  padding: 15px;
  background-color: #59b256;
  color: white;
  font-weight: 500;
  cursor: pointer;
  border-radius: 10px;
  transition: all 1s;
  width: fit-content;
}

button:hover {
  background: green;
  transition: all 1s ease-in;
}

.social {
  display: flex;
  gap: 1.5rem;
}

.msg {
  border-radius: 10px;
  border: 1px solid black;
}

.linkedin {
  font-size: 2.5rem;
  color: #0b65c2;
  margin-left: 1rem;
  margin-top: 2rem;
}

.github {
  font-size: 2.5rem;
  color: #000000;
  margin-top: 2rem;
}

.twitter {
  font-size: 2.5rem;
  margin-top: 2rem;
  background: radial-gradient(
    circle at 30% 107%,
    #fdf497 0%,
    #fdf497 5%,
    #fd5949 45%,
    #d6249f 60%,
    #285aeb 90%
  );
  -webkit-background-clip: text;
  background-clip: text;
  -webkit-text-fill-color: transparent;
}


@media screen and (max-width: 480px) {
  .c-wrapper {
    flex-direction: column;
    padding: 0px 50px;
  }

  .c-title {
    font-size: 30px;
  }

  .c-info-item {
    width: 100%;
  }

  .c-desc {
    display: none;
  }

  form {
    margin-top: 0px;
    display: flex;
    flex-wrap: wrap;
  }

  input {
    width: 70%;
    height: 40px;
    margin: 10px;
    margin-left: 0;
  }


  button {
    margin-top: 10px;
  }
}