.about{
    height: 60vh;
    display: flex;
    margin-bottom: 10rem;
    margin-top: -10rem;
}

.a-left{
    flex: 1;
    display: flex;
    align-items: center;
    justify-content: sta;
    position: relative;
    height: 100%;
}

.a-title{
    font-size: 3rem;


}
.a-right{
    display: flex;
    flex-direction: column;
    justify-content: center;
    padding-right: 4rem;
    flex: 1;
}

.a-img{
    position: absolute;
    border: none;
    width: 80%;
    height: 100%;
}

.a-sub{
    padding-top: 1.5rem;
}

.a-desc{
    padding-top: 1.5rem;
}


@media screen and (max-width:480px) {
    .a{
      flex-direction: column;
      text-align: center;
      margin-top: 50px;
    }

    .a-left {
      width: 100%;
      display: none;
    }

    .a-card {
      height: 30vh;
    }

    .a-card.bg, .a-award {
      display: none;
    }

    .a-right{
      padding: 20px;
    }
}
