.i{
    display: flex;
    height: 100vh;

}

.i-left{
    flex: 1;
    display: flex;
    align-items: center;
    padding-bottom: 5rem;
}

.i-right{
    flex: 1;
}

.i-right:hover{
    cursor: none;
    pointer-events: none;
}

.i-left-wrapper{
    padding-left: 2rem;
    height: 50%;
    display: flex;
    flex-direction: column;
    gap: 2rem;
}

.i-intro{
    font-size: 30px;
    font-weight: 300;
}

.i-name{
    font-size: 60px;
}


.i-title{
    height: 50px;
    overflow: hidden;
}



.i-title-wrapper{
    height: 100vh;
}


.i-title-item{
    height: 50px;
    font-size: 30px;
    font-weight: bold;
    color: #59b256;
    display: flex;
    align-items: center;
    
}

.i-img{
    border: none;
    width: 100vh;
    height: 60vh;
    margin-top: 0.5rem;
    pointer-events: none;
}


.i-bg{
    background-color: #59b256;
    width: 100%;
    height: 100%;
    clip-path: circle(50% at 50% 50%);
    position: absolute;
}

.i-scroll{
    width: 30px;
    height: 30px;
    position: absolute;
    bottom: 20px;
}


@media screen and (max-width: 480px) {
    .i {
      flex-direction: column;
    }
  
    .i-left-wrapper {
    
      padding: 10px;
      align-items: center;
      justify-content: center;
      text-align: center;
      height: 100%;
    }
    .i-desc{
      display: none;
    }

    .i-right{
        display: none;
    }
    
  }