.pc{
    padding: 50px 100px;
    display: flex;
    flex-direction: column;
    align-self: center;
    text-align: center;
    pointer-events: none;
    transition: all 0.3s ease;
    transform: translate3d(0, 0, 0);

}

.pl-title{
    font-size: 50px;
    font-weight: 600;
}

.pl-list{
    width: 100%;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    transition: all 0.3s ease;
    transform: translate3d(0, 0, 0);
}
